
.inputFeild{
    justify-content: center;
    height: 40px;
    width:200px;
    background-color: blanchedalmond;
    text-decoration-color: aliceblue;
    border-radius: 40px;
    margin-top: 0;
    padding: 20px;
    text-align: center;
    font-family: 'Belanosima', sans-serif;
}
p{
    font-family: 'Belanosima', sans-serif;
}

.box{
   padding: 50px 50px ;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border: 1px solid black;
    margin-top: 80px;
    margin-bottom: 100px;
    margin-right: 100px;
    margin-left: 100px;
    background-color: lightblue;
   
}
@media(max-width: 600px){
    
.inputFeild
{
justify-content: center;
width: 80%;
height: 2rem;
}    
.box{
    margin: 0%;
    height: 71.9vh;
}
}